@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-display: swap;
  src: local("Opensans regular"), url("../fonts/opensans.woff") format("woff"), url("../fonts/opensans.woff2") format("woff2");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-display: swap;
  src: local("Opensans bold"), url("../fonts/opensansbold.woff") format("woff"), url("../fonts/opensansbold.woff2") format("woff2");
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  color: #000000;
  background-color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.6;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}
.title {
  margin: 0 0 20px;
  text-transform: uppercase;
  font-size: 27px;
  text-align: center;
}
.title--white {
  color: #ffffff;
}
@media (min-width: 768px) {
  .title {
    font-size: 24px;
  }
}
.header {
  background: linear-gradient(to right, #60f0dd, #00b0c7);
  position: fixed;
  width: 100%;
  right: 0;
  border-bottom: 3px solid #fff;
}
.header__wrap {
  max-width: 1200px;
  margin: 0 auto;
  padding: 25px 40px 20px;
}
@media (min-width: 768px) {
  .header__wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__top-wrap {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
.header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .header__toggle {
    display: block;
    font-size: 0;
    line-height: 0;
    color: transparent;
    width: 30px;
    height: 30px;
    background-image: url(../img/icon-menu-open.svg);
    background-repeat: no-repeat;
    background-size: 30px;
    background-color: transparent;
    border: 0;
    cursor: pointer;
  }
}
.header__toggle--show {
  background-image: url(../img/icon-menu-close.svg);
}
@media (max-width: 767px) {
  .main-nav--hidden {
    display: none;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
  }
}
.main-nav__link {
  font-size: 18px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
  padding: 0 10px;
  transition: 0.2s;
}
.main-nav__link:hover,
.main-nav__link:focus {
  opacity: 0.7;
}
.main-nav__link:active {
  opacity: 0.5;
}
.about-me {
  padding: 95px 20px 20px;
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 768px) {
  .about-me {
    padding: 105px 30px 30px;
  }
}
.about-me__wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .about-me__wrap {
    align-items: flex-start;
    flex-direction: row;
  }
}
.about-me__img {
  margin-bottom: 10px;
  width: 200px;
  height: 200px;
  border: 5px solid #10856e;
  border-radius: 10%;
}
.about-me__info-wrap {
  margin-left: 20px;
}
.about-me__info-wrap p {
  font-size: 20px;
  margin: 0;
}
@media (min-width: 768px) {
  .about-me__info-wrap p {
    font-size: 28px;
  }
}
.about-me__name {
  font-size: 20px;
  margin: 0;
}
@media (min-width: 768px) {
  .about-me__name {
    font-size: 28px;
  }
}
.about-me__links-wrap {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .about-me__links-wrap {
    align-self: flex-start;
    margin-left: auto;
  }
}
.about-me__link {
  transition: 0.2s;
}
.about-me__link:hover .about-me__link-img,
.about-me__link:focus .about-me__link-img {
  fill: #10856e;
}
.about-me__link:active .about-me__link-img {
  fill: #1d1d1d;
}
.skills {
  background: linear-gradient(to right, #60f0dd, #00b0c7);
}
.skills__wrap {
  padding: 80px 20px 20px;
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 768px) {
  .skills__wrap {
    padding: 80px 30px 30px;
  }
}
.skills__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  font-size: 18px;
  font-weight: 700;
}
.skills__item {
  width: 100%;
  min-height: 56px;
  padding-left: 30px;
}
@media (min-width: 768px) {
  .skills__item {
    width: 40%;
  }
}
.portfolio {
  padding: 80px 20px 20px;
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 768px) {
  .portfolio {
    padding: 80px 30px 30px;
  }
}
.cards {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.cards__item {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 15px;
}
.cards__img {
  width: 280px;
  height: 230px;
}
.cards__active {
  display: flex;
  justify-content: space-around;
}
.cards__link {
  padding: 15px 0;
  margin: 0 5px;
  display: flex;
  justify-content: center;
  flex-grow: 1;
  font-weight: 700;
  color: #000000;
  border: 1px solid #000000;
  text-transform: uppercase;
  transition: 0.3s;
}
.cards__link:hover,
.cards__link:focus {
  background-color: #444444;
  color: #ffffff;
}
.education {
  background: linear-gradient(to right, #60f0dd, #00b0c7);
}
.education__wrap {
  padding: 80px 20px 20px;
  margin: 0 auto;
  max-width: 1200px;
}
@media (min-width: 768px) {
  .education__wrap {
    padding: 80px 30px 30px;
  }
}
.education__desc {
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  color: #ffffff;
}
.education__link {
  color: #2a7777;
  opacity: 0.5;
  transition: 0.3s;
}
.education__link:hover,
.education__link:focus {
  opacity: 1;
}
.education__link:active {
  color: blue;
}
.education__sub-title {
  text-align: center;
  color: #ffffff;
}
.certificates {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -5px;
  justify-content: space-around;
}
.certificates__item {
  width: 270px;
  margin: 0 5px;
}
.certificates__title {
  text-align: center;
}
.certificates__link {
  display: block;
  transition: 0.3s;
}
.certificates__link:focus,
.certificates__link:hover {
  transform: translate(-5px, -5px);
  background-color: rgba(255, 255, 255, 0.35);
}
.certificates__title {
  color: #222e30;
  padding-bottom: 10px;
}
.contacts {
  background: linear-gradient(to right, #6599ad, #458ba7);
}
.contacts__wrap {
  padding: 40px 20px 20px;
  margin: 0 auto;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .contacts__wrap {
    padding: 50px 30px 30px;
  }
}
.contact__link {
  font-size: 18px;
  color: #222e30;
  font-weight: 700;
  transition: 0.2s;
}
.contact__link:focus,
.contact__link:hover {
  transform: translateY(5px);
  opacity: 0.6;
}
.contact__link:active {
  opacity: 1;
  transform: none;
}
.social {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
.social__link {
  margin: 0 10px;
  transition: 0.3s;
  border-radius: 50%;
  opacity: 0.7;
}
.social__link:hover,
.social__link:focus {
  transform: translateY(-5px);
  opacity: 0.9;
}
.social__link:active {
  opacity: 1;
  transform: translateY(0);
}
